import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
var QQMapWX = require('../../utils/qqmap-wx-jssdk.min.js');
var qqmapsdk;
export default {
	data() {
		return {
			user_info:'',
			latitude: '',
			longitude: '',
			dangqian: '',
			data:[],
			linkman:'',
			address_count:0,
		}
	},
	/**
	 * 生命周期函数--监听页面初次渲染完成
	 */
	onReady() {
		this.mapCtx = wx.createMapContext('myMap');
	},
	onLoad() {
		this.loadMap();
	},
	onShow(){
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.loadData();
		} else {
			wx.reLaunch({
				url: '/pages/index/index',
			})
		}	
	},
	methods: {
		loadData(){
			this.request({
				url: interfaces.adrList,
				header: {
					token:this.user_info.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						this.data=res.data;
						this.address_count=res.data.adr.length;
						if(this.address_count>0){
							this.linkman=res.data.adr[0].linkman;
						}
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
		loadMap() {
			qqmapsdk = new QQMapWX({
				key: 'WCYBZ-EWVC4-CC3U2-DZGZI-UWGK2-E2BMM'
			});
			wx.getLocation({
				type: 'gcj02',
				success: res => {
					qqmapsdk.reverseGeocoder({
						location: {
							latitude: res.latitude,
							longitude: res.longitude
						},
						success: addressRes => {
							this.latitude = res.latitude,
							this.longitude = res.longitude,
							this.dangqian = addressRes.result.address_component.city
						}
					})
				},
				fail: err => {
					//console.log(err);
					if (err.errMsg === "getLocation:fail auth deny") {
						wx.showModal({
							title: '提示',
							showCancel: false,
							confirmText: '好的',
							content: '未获取到你的地理位置，暂时无法为你提供服务。请检查是否开启定位权限。或尝试重新打开小程序。',
							success: (res) => {
								if (res.confirm) {
									wx.openSetting({
										success(settingdata) {
											//console.log(settingdata)
											if (settingdata.authSetting['scope.userLocation']) {
												wx.showToast({
													title: '授权成功',
													icon: 'none',
													mask: true,
												})
											} else {
												wx.showToast({
													title: '授权失败',
													icon: 'none',
													mask: true,
												})
											}
										}
									})
								}
							}
						})
					}
				}
			});
		},
		//设为默认地址
		setDefault(id,index){
			this.request({
				url: interfaces.setDefaultAdr,
				header: {
					token:this.user_info.token
				},
				data:{
					id:id
				},
				method: 'POST',
				success: res => {
					//console.log(res);
					if (res.code == 200) {
						for(var k in this.data.adr){
							this.data.adr[k].dft=0;
						}
						this.data.adr[index].dft=1;
						this.linkman=this.data.adr[index].linkman;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
		//跳转去添加地址页面
		addAdr(){
			if(this.address_count==0){
				wx.navigateTo({
					url: '../address/editstore',
				})
			}else{
				wx.navigateTo({
					url: '../address/adduser',
				})
			}
		},
		order(){
			if(this.address_count==0){
				wx.showToast({
					title: '请先添加服务地址',
					icon: "none",
				});
				setTimeout(function() {
					wx.navigateTo({
						url: '../address/editstore',
					})
				}, 1500);
			}else{
				if(this.data.store.position_area_id==0){
					wx.showToast({
						title: '请补全门店信息',
						icon: "none",
					});
					setTimeout(function() {
						wx.navigateTo({
							url: '../address/editstore',
						})
					}, 1500);
				}else{
					wx.navigateTo({
						url: '../order/add',
					})
				}
			}
		},
		//移到自己定位坐标
		moveToLocation: function() {
			this.mapCtx.moveToLocation()
		},
	}
}
